body{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}
h1 {
  font-family: Courier New, monospace;
  font-size: 10vw;
  color: rgb(248, 211, 215);
}
.first-page-header{
  text-align: center;
  font-family: Courier New, monospace;
  text-shadow: 5px 5px #ffe2e7; 
}
.first-page-header-small{
  font-size: 6.5vw;
  color: rgb(244, 191, 197);
}
.cats-container{
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 3%;
}
.cats-card{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: end;
  background-color: rgb(250,218,221);
}
.cats-div-images{
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
}
.cats-img{
  max-width: 90%;
  max-height: 90%;
}
.loading{
  display: flex;
  justify-content: center;
}
@media (max-width:425px){
  .cats-container{
    grid-template-columns: 1fr 1fr;
    grid-gap: 5%;
  }
  h1 {
    font-size: 20vw;
  }
}