.pagination {
  display: flex;
  margin-top: 10vw;
  margin-bottom: 7vw;
  justify-content: center;
}
.pagination-item{
  text-align: center;
  color: rgba(0, 0, 0, 0.973);
  display: flex;
  box-sizing: border-box;
  align-items: center;
  letter-spacing: 0.01071em;
  border-radius: 50%;
  line-height: 1.43;
  font-size: 2vw;
  min-width: 5vw;
  justify-content: center;
}
.dots:hover {
  background-color: transparent;
  cursor: default;
}
.pagination-item:hover {
  background-color: rgb(253, 195, 234);
  cursor: pointer;
}
.selected {
  background-color: rgb(233, 170, 212);
}
.arrow::before {
  position: relative;
  content: '';
  display: inline-block;
  width: 0.4em;
  height: 0.4em;
  border-right: 0.12em solid rgba(0, 0, 0, 0.87);
  border-top: 0.12em solid rgba(0, 0, 0, 0.87);
}
.left {
  transform: rotate(-135deg) translate(-50%);
}
.right {
  transform: rotate(45deg);
}
.disabled{
  display: none;
}
.disabled::before {
  border-right: 0.12em solid rgba(0, 0, 0, 0.43);
  border-top: 0.12em solid rgba(0, 0, 0, 0.43);
}
.disabled:hover {
  background-color: transparent;
  cursor: default;
}
.pagination-bar {
  display: flex;
  justify-content: center;
  padding: 0;
}
@media (max-width:425px){
  .pagination-item{
    letter-spacing: 0.03em;
    font-size: 10vw;
    min-width: 10vw;
  }
  .pagination {
    margin-top: 40vw;
    margin-bottom: 20vw;
  }
}
  
  
     
  
    
  
  
      

  